.base-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header{
    font-size: 24px;
    font-family: Georgia, 'Times New Roman', Times, serif;    
}

.content{
    display: flex;
    flex-direction: column;
}
.image{
    width: 21em;
}
.image > img{
    width: 100%;
    height: 100%;
}
.form{
    margin-top: 2em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form .form-group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.form .form-group > label{
    font-size: 20px;
}

.form .form-group > input{
    margin-top: 5px;
    min-width: 18em;
    height: 37px;
    padding: 0px 10px;
    font-size: 16px;
    font-family: Georgia, 'Times New Roman', Times, serif;
    background-color: #f3f3f3;
    border: 0;
    border-radius: 4px;
    margin-bottom: 1em;
    transition: all 250ms ease-in-out;
}

input:hover{
    outline: none;
    box-shadow: 0px 0px 10px 0.9px #0e81ce96;
}
.footer{
    margin-top:1em;
}

.footer .btn{
    font-size: 21px;
    padding: 5px 20px;
    border: 0;
    background-color: #3498db;
    color: #fff;
    border-radius: 3px;
    transition: all 250ms ease-in-out;
    cursor: pointer;
}

.footer .btn:hover{
    background-color: #2386c8;
}
.footer .btn:focus{
    outline: none;
}
